.carddd {
  margin-top: 10px;
  display: flex;
  height: 200px;
  width: 900px;
  justify-content: space-between;
  border-radius: 15px;
  box-shadow: 0 0 10px 1px rgb(196, 192, 192);
  .left {
    width: 200px;
    height: 190px;
    .imggg {
      border-top-left-radius: 15px;
      border-bottom-left-radius: 15px;
      width: 100%;
      background-color: rgb(230, 230, 230);
    }
    .imggg1 {
      border-top-left-radius: 15px;
      border-bottom-left-radius: 15px;
      margin-top: -25px;
      width: 100%;
      background-color: rgb(225, 224, 224);
    }
  }
  .right {
    gap: 0px;
    display: flex;
    .text {
      padding-top: 20px;
      padding-bottom: 10px;
      .h1 {
        font-size: 25px;
        font-weight: 800;
      }
      .texts__p {
        color: rgb(79, 79, 79);
        font-weight: 700;
        width: 400px;
        font-size: 13px;
      }
      .texts_price {
        color: rgb(0, 0, 0);
        font-weight: 900;
      }
    }
    .clicks {
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      width: 250px;
      background-color: rgb(225, 224, 224);

      .btns {
        justify-content: center;
        padding: 20px;
        display: flex;
        gap: 10px;

        .p {
          color: red;
          font-size: 20px;
          font-weight: 900;
          padding-top: 10px;
        }
      }
      .btn_plus {
        width: 40px;
        height: 40px;
        margin-top: 60px;
        font-size: 25px;
        font-weight: 900;
        border: none;
        border-radius: 50%;
        text-align: center;
        align-items: center;
      }
      .delete {
        margin-left: 12px;
        padding: 10px;
        text-align: center;
        font-size: 20px;
        color: white;
        width: 90%;
        border-radius: 15px;
        background-color: rgb(196, 28, 28);
      }
    }
  }
}
  .btnd {
    text-align: center;
    align-items: center;
    border-radius: 20px;
    padding-left: 10px;
    margin-left: 220px;
    justify-content: center;
    margin-top: 50px;
    width: 400px;
    height: 250px;
    border: 2px solid rgb(179, 9, 9);
    box-shadow: 0 0 20px 1px rgb(255, 163, 163);
    .h11{
        padding-top: 20px;
        width: 200px;
    }
    .oplata {
      display: flex;
      gap: 20px;
        text-align: center;
      .hh1 {
        margin-top: 10px;
        font-size: 25px;
        font-weight: 900;
      }
      .obshi{
        font-weight: 900;
        color: red;
        padding-top: 15px;
        font-size: 20px;
      }
    }
    .zakaz_btn {
        margin-top: 10px;
        color: white;
        border-radius: 10px;
        height: 35px;
        background-color: rgb(179, 18, 18);
        border: none;
      width: 200px;
    }
  
