.inppp{
  display: flex;
  justify-content: center;
  .inputt{
  margin-top: 150px;
  width: 800px;
  padding: 10px;
  height: 40px;
  border-radius: 20px;
  border: 2px solid rgb(196, 17, 17);
}
}

.maped {
  margin-top: 130px;
  width: 100%;
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(3, 1fr);

  .cardPlants {
    border: 1px solid rgb(202, 202, 202);
    width: 425px;
    display: flex;
    border-radius: 10px;
    height: 190px;
    margin-top: 20px;
    .cardPlants__img {
      padding-top: 20px;
      padding-bottom: 20px;
      width: 150px;
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
      background-color: rgb(220, 217, 217);
      transition: 0.4s;
    } 
    .ggg {
      padding: 20px;
      .cardPlants__h1 {
        font-size: 15px;
        font-weight: 600;
        color: black;
      }
      .cardPlants__p {
        font-size: 9px;
        color: rgb(55, 55, 55);
      }
      .prices {
        display: flex;
        gap: 50px;
        .prices__1 {
          color: rgb(0, 0, 0);
          font-weight: 800;
        }
        .prices__2 {
          padding-top: 5px;
          color: red;
          font-weight: 600;
          font-size: 14px;
          text-decoration: line-through;
        }
      }
      .price__btn {
        width: 220px;
        border: none;
        background-color: rgb(196, 17, 17);
        color: white;
        border-radius: 8px;
      }
    }
  }
}
